.stars {
  display: flex;

  &__star {
    height: 16px;
    width: 16px;
    margin-right: 4px;

    background-image: url(\images\star.svg);
    background-position: center;
    background-repeat: no-repeat;
  }

  &:last-child {
    margin-right: 0;
  }

  @for $i from 1 through 5 {
    &--#{$i} > :nth-child(-n + #{$i}) {
      background-image: url(\images\star-active.svg);
    }
  }
}
